.ground-banner {
  max-width: 1200px;
  margin: auto;
  position: relative;
  flex-wrap: wrap;
}
.ground-banner .list-banner {
  text-align: center;
  justify-items: center;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1200px;
  margin: 0 auto;
}
.ground-banner .list-banner .list-item-banner .slick-dots {
  bottom: 0;
  margin: 10px auto;
}

.ground-banner .list-banner .items-banner {
  max-width: 1200px;
  margin: auto;
  height: auto;
  position: relative;
  display: block;
}
.ground-banner .list-banner .items-banner img {
  position: relative;
  width: 100%;
  height: 100%;
}

.ground-banner .list-banner .slick-next,
.ground-banner .list-banner .slick-prev {
  display: none !important;
}

@media screen and (max-width: 768px) {
  .slick-next,
  .slick-prev {
    width: 25px;
    height: 60px;
  }
  .slick-next:before,
  .slick-prev:before {
    font-size: 18px;
  }
}

@media screen and (max-width: 488px) {
  .slick-next,
  .slick-prev {
    width: 20px;
    height: 50px;
  }
  .slick-next:before,
  .slick-prev:before {
    font-size: 15px;
  }
}

@media screen and (max-width: 320px) {
  .slick-next,
  .slick-prev {
    width: 18px;
    height: 45px;
  }
  .slick-next:before,
  .slick-prev:before {
    font-size: 13px;
  }
}
